/* Layouts */
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  font-size: 22px;
  line-height: 34px;
  font-weight: normal;
  font-style: normal;
  background-color: #ffffff; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  margin: 0 auto;
  max-width: 1136px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 14px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Blocks */
.page__body {
  position: relative; }

.page__body::before {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 825px;
  content: "";
  background-color: #1a1a1a;
  background-image: url("../img/cover-desktop.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover; }

.main-header__navigation {
  display: flex;
  flex-direction: column; }

.logo {
  position: relative;
  margin-left: 126px;
  margin-top: 226px;
  order: 2; }

.logo svg {
  position: relative;
  top: 21px; }

.site-navigation {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 17px 19px;
  padding-bottom: 9px;
  width: 874px;
  list-style: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.site-navigation__item a {
  color: #ffffff;
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  letter-spacing: -0.005em;
  opacity: 0.7; }
  .site-navigation__item a:focus, .site-navigation__item a:hover {
    opacity: 1; }
  .site-navigation__item a:active {
    opacity: 0.3; }

.promo {
  margin: 0 auto;
  width: 880px;
  min-height: 500px;
  color: #ffffff; }

.promo__inner {
  display: flex;
  justify-content: space-between; }

.promo__title {
  margin: 0;
  margin-top: 46px;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px;
  letter-spacing: -0.005em; }

.promo__text {
  margin: 0;
  margin-top: 24px;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: -0.005em;
  opacity: 0.8; }

.promo_form {
  width: 326px; }

.promo__description {
  width: 393px; }

.form__title {
  margin: 0;
  color: #ffffff;
  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  text-align: center;
  letter-spacing: -0.005em; }

.form__field {
  margin: 0;
  margin-top: 20px; }

.form input {
  padding: 14px 30px;
  padding-bottom: 10px;
  width: 100%;
  height: 48px;
  color: #1c1c1c;
  font-size: 15px;
  line-height: 24px;
  background-color: #ffffff;
  border: 1px solid #c9ccd4;
  border-radius: 4px; }
  .form input:hover, .form input:focus {
    border-color: #000000; }

.form input[type="submit"] {
  margin-top: 20px;
  padding: 0;
  color: #000000;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  background-color: #0ad9c6;
  border: none; }
  .form input[type="submit"]:focus, .form input[type="submit"]:hover {
    opacity: 0.7; }
  .form input[type="submit"]:active {
    opacity: 0.5; }

.main-footer {
  padding-bottom: 96px;
  text-align: center; }
  @media (max-width: 1023px) {
    .main-footer {
      padding-bottom: 52px; } }
  @media (max-width: 767px) {
    .main-footer {
      position: relative; } }

.main-footer h2 {
  margin: 0;
  padding: 0;
  color: #1f1f1f;
